import React from "react";
import "./hero.css";
import profile from "../../assets/profile.jpg";
import polygon from "../../assets/polygon-scatter.svg";
import logo_linkedin from "../../assets/linkedin.png";
import logo_twitter from "../../assets/twitter.png";
import logo_github from "../../assets/github.png";
import logo_instagram from "../../assets/instagram.png";
const Hero = () => {

  return (
    <div className="hero" id="home">
      <div className="content">
        <div className="content-div">
          <p className="salutation"> Hello, I'm</p>
          <h1>Faheem Hamid</h1>
          <p className="description">Full stack Web Developer (MERN)</p>
          <p className="about">
            A tech-savvy solution sensei well-versed in full-stack web
            development, passionate about crafting innovative products that have
            a positive impact on the world.
          </p>


          <button className="Download_resume"  
          onClick={()=>{window.open(`https://drive.google.com/file/d/1hB3kl2F9_duGtmTS5loqfV3qpfQGk5fc/view?usp=sharing`)}}  id="resume-button-2">
           
           <a id="resume-link-2"
           href="Faheem-Hamid-Resume.pdf"
           target="_blank" 
            download="Faheem-Hamid-Resume.pdf"
           
           >Download Resume</a>
           
          </button>

          <div className="socials">
            <a
              href="https://twitter.com/faheemw09"
              target="_blank"
              className="social"
            >
              <img src={logo_twitter} alt="Twitter" />
            </a>
            <a
              href="https://www.linkedin.com/in/faheem-hamid-529403160"
              target="_blank"
              className="social"
              id="contact-linkedin"
            >
              <img src={logo_linkedin} alt="Linkedin" />
            </a>
            <a
              href="https://github.com/Faheemw09"
              target="_blank"
              id="contact-github"
              className="social"
            >
              <img src={logo_github} alt="Github" />
            </a>
            <a
              href="https://www.instagram.com/faheemw09/?"
              target="_blank"
              className="social"
            >
              <img src={logo_instagram} alt="Instagram" />
            </a>
           
          </div>
        </div>
      </div>
      <div className="image">
        <div className="profile-img-div">
          <div className="lines"></div>
          <img src={profile} alt="profile" className="home-img" />
        </div>
        <div className="polygons">
          <img src={polygon} />
        </div>
      </div>
    </div>
  );
};
export default Hero;
