import React, { useState } from "react";
import "./navbar.css";
import Logo from "../../assets/Logo.svg";
import { FiAlignRight } from "react-icons/fi";

const Links = ({ closeMenu }) => {
    return (
        <div>
            <a href="#home" id="link" className="nav-link home" onClick={closeMenu}>
                Home
            </a>

            <a href="#about" id="link" className="nav-link" onClick={closeMenu}>
                About me
            </a>

            {/* */}
            <a href="#skills" id="link" onClick={closeMenu}>
                Skills
            </a>

            <a href="#projects" id="link" className="nav-link projects" onClick={closeMenu}>
                Projects
            </a>
            <a href="#contact" id="link" className="nav-link" onClick={closeMenu}>
                Contact
            </a>

            <button
                id="resume-button-1"
                onClick={() => {
                    window.open(`https://drive.google.com/file/d/1hB3kl2F9_duGtmTS5loqfV3qpfQGk5fc/view?usp=sharing`);
                    closeMenu();
                }}
            >
                <a
                    href="Faheem-Hamid-Resume.pdf"
                    target="_blank"
                    download={"Faheem-Hamid-Resume.pdf"}
                    id="resume-link-1"
                    className="nav-link resume"
                    onClick={closeMenu}
                >
                    Resume
                </a>
            </button>
        </div>
    );
};

const Navbar = () => {
    const [toggleMenu, setToggleMenu] = useState(false);

    const toggleMenuHandler = () => {
        setToggleMenu(!toggleMenu);
    };

    const closeMenu = () => {
        setToggleMenu(false);
    };

    return (
        <div id="nav-menu" className="navbar">
            <div className="title">
                <img src={Logo} alt="logo" />
                <p className="nav__header">Portfolio</p>
            </div>
            <div id="links">
                <Links closeMenu={closeMenu} />
            </div>
            <div className="toggle-links" onClick={toggleMenuHandler}>
                <FiAlignRight className="hamburger-icon" />
            </div>
            {toggleMenu ? (
                <div className="toggle-menu">
                    <Links closeMenu={closeMenu} />
                </div>
            ) : (
                <></>
            )}
        </div>
    );
};

export default Navbar;
